<template>
    <div>
        <div v-if="show" style="position:fixed;top:0;left:0;width:100vw;height:100vh;background:black;opacity:0.3;">

        </div>
        <div v-if="show" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:10;">
            <div style="width:100%;" class="bg-white p-3 shadow-md rounded-lg p-3">
                <p class="text-xl p-3">
                    <!-- {{pilih.translation_id}} -->
                </p>
            </div>
        </div>
        <div class="container" >
            <div class="row justify-content-center sm:p-4">
                <div class="col-12">
                    <br />
                    <p class="p-3 my-2 text-center text-2xl font-bold font-italic bg-white  mt-3 rounded-lg shadow">Ayat Kursi</p>
                    <div class="row">
                        <div class="col-sm-6 ">
                        <!-- <div class="input-group rounded-lg shadow mb-2 bg-white p-4">
                            <div v-if="listPencarian.length>0">
                                <p class="font-semibold p-2">Index Pencarian : </p>
                                <v-select :options="listPencarian" style="min-width:300px;" label="title" v-model="cari" :reduce="e => e.value"></v-select>
                            </div>
                        </div> -->
                        </div>
                        <div class=" col-sm-12 pl-3 pr-3">
                        <div class="bg-white  rounded-lg shadow p-4">
                            <p class="font-semibold p-2">Ukuran huruf : </p>
                            <div class="sm-form ">
                            <select
                                class="form-control text-sm"
                                style="height: 34px; width: 200px"
                                v-model="pilihanLebar"
                            >
                                <option
                                v-for="(item, index) in lebar"
                                :key="index"
                                :value="item"
                                >
                                {{ index + 1 }}
                                </option>
                            </select>
                            </div>
                        </div>
                        </div>
                    </div>
                    </div>
                <div class="col-12 col-sm-12 cursor-pointer" @click="pilih=item;show=!show;" v-for="(item, index) in td" :key="index+'asmaulhusna'" v-show="index<1">
                    <div class="rounded-lg shadow bg-white  p-2 sm:p-5 mt-3">
                        <p class="pb-3 text-3xl font-italic">
                            {{item.nama}}
                        </p>
                        <p class="pb-3 text-center "  :class="`${pilihanLebar}`" style="font-family: 'Amiri', serif;line-height: 2.0;">
                            {{item.arab}}
                        </p>
                        <p class="pb-3 text-lg font-italic">
                            {{item.latin}}
                        </p>
                        <p class="pt-2 text-sm">
                            {{item.arti}}
                        </p>
                        <!-- <span>
                            {{item.index}}
                        </span>
                        <p class="text-center text-4xl" style="font-family: 'Amiri', serif;line-height: 2.0;">
                            {{item.arabic}}
                        </p>
                        <p class="text-sm text-center mt-2">
                            {{item.latin}}
                        </p> -->
                        <p></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    computed:{
        td(){
            let that=this;
            let data =this.datanya;
            data=data.filter(e=>{
                if(e.nama.toString().toLowerCase().indexOf(that.cari)!=-1){
                    return e;
                }
            })
            return data;
        }
    },
    layout:'app_islamic',
    data(){
        return{
            cari:'',
             lebar:[
                'text-lg',
                'text-xl',
                'text-2xl',
                'text-4xl',
                'text-5xl',
            ],
            pilihanLebar:'text-2xl',
            datanya:[
                {
                    "nama": "Ayat Kursi",
                    "arab": "ٱللَّهُ لَآ إِلَـٰهَ إِلَّا هُوَ ٱلْحَىُّ ٱلْقَيُّومُ‌ۚ لَا تَأْخُذُهُۥ سِنَةٌ وَلَا نَوْمٌ‌ۚ لَّهُۥ مَا فِى ٱلسَّمَـٰوَٲتِ وَمَا فِى ٱلْأَرْضِ‌ۗ مَن ذَا ٱلَّذِى يَشْفَعُ عِندَهُۥٓ إِلَّا بِإِذْنِهِۦ‌ۚ يَعْلَمُ مَا بَيْنَ أَيْدِيهِمْ وَمَا خَلْفَهُمْ‌ۖ وَلَا يُحِيطُونَ بِشَىْءٍ مِّنْ عِلْمِهِۦٓ إِلَّا بِمَا شَآءَ‌ۚ وَسِعَ كُرْسِيُّهُ ٱلسَّمَـٰوَٲتِ وَٱلْأَرْضَ‌ۖ وَلَا يَــُٔودُهُۥ حِفْظُهُمَا‌ۚ وَهُوَ ٱلْعَلِىُّ ٱلْعَظِيمُ",
                    "latin": "Allahu laaa ilaaha illaa Huwal Haiyul Qaiyoom; laa taakhuzuhoo sinatunw wa laa nawm; lahoo maa fissamaawaati wa maa fil ard; man zal lazee yashfa'u indahooo illaa bi-iznih; ya'lamu maa baina aydeehim wa mww khalfahum wa laa yuheetoona bishai'im min 'ilmihee illaa bimaa shaaa'; wasi'a Kursiyyuhus samaawaati wal arda wa laa ya'ooduho hifzuhumaa; wa Huwal Aliyyul 'Azeem",
                    "arti": "“Allah, tidak ada tuhan selain Dia. Yang Mahahidup, Yang terus menerus mengurus (makhluk-Nya), tidak mengantuk dan tidak tidur. Milik-Nya apa yang ada di langit dan apa yang ada di bumi. Tidak ada yang dapat memberi syafaat di sisi-Nya tanpa izin-Nya. Dia mengetahui apa yang di hadapan mereka dan apa yang di belakang mereka, dan mereka tidak mengetahui sesuatu apa pun tentang ilmu-Nya melainkan apa yang Dia kehendaki. Kursi-Nya meliputi langit dan bumi. Dan Dia tidak merasa berat memelihara keduanya, dan Dia Mahatinggi, Mahabesar."
                },            
            ],
            show:false,
            pilih:{}
        }
    },
    mounted() {
        let that=this;
        function onBackKeyDown(e){
          e.preventDefault();
          e.stopPropagation();
          that.$router.push('/app/islamic')
        };
        document.addEventListener("backbutton", onBackKeyDown, false);
    },
      metaInfo: {
        title: `Baca Al Qur'an`,
        link: [
         { rel: 'preconnect', href: 'https://fonts.googleapis.com' },
         { rel: 'preconnect', href: 'https://fonts.gstatic.com' },
         { rel: 'stylesheet', href: 'https://fonts.googleapis.com/css2?family=Amiri&display=swap' },
        ],
    },
}
</script>